body {
  margin: 0;
  background-color: turquoise;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  overflow: auto;
}

button{
  cursor: pointer;
}

.nav-btn {
  font-size: 16px;
}

.home0-btn{
  width: 200px;
  height: 90px;
  font-size: 30px;
}

.teacher-input {
  margin-left: 30%;
  margin-top: 60px;
  height: 50px;
  width: 550px;
  font-size: 20px;
}

.enter-sen-btn{
  margin-top: 60px;
  margin-right: 150px;
  width: 90px;
  height: 50px;
  font-size: 20px;
  float: right;
}

.home-sents-edit-btn{
  margin-left:10px;
  font-size:18px;
}

.list-btn {
  width: 120px;
  height: 50px;
  font-size: 20px;
}

.start-btn {
  font-size: 20px;
}

.pls-mns-ps-btns {
  outline:none;
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  font-size: 16px;
}

.refresh-btn {
  outline:none;
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  font-size: 18px;
}

.info-btn {
  outline:none;
  float: right;
  color: darkblue;
  background-color: Transparent;
  background-repeat:no-repeat;
  border: none;
  font-size: 18px;
}

#overlayhome0 {
  position: fixed; /* Sit on top of the page content */
  display: none; /* Hidden by default */
  width: 100%; /* Full width (cover the whole page) */
  height: 100%; /* Full height (cover the whole page) */
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.712); /* Black background with opacity */
  z-index: 2; /* Specify a stack order in case you're using a different order for other elements */
}

